//Basic React import
import React, {Component} from 'react';
import {Context} from '../../context/isDesktopContext';
import Typist from 'react-typist';

class Footer extends Component{
	render(){
		let isDesktop = this.context;
		return(
			isDesktop ? <footer className="pl-2 mt-3 fixed-bottom">
            <Typist cursor={{show:false}} startDelay={3000}>
              <span className="h4">&gt;Omer Marquez 2020</span>
            </Typist>
          </footer> : ''
		);
	}
}

Footer.contextType = Context;

export default Footer;