//Basic React import
import React, {Component} from 'react';
const Context = React.createContext();


class ContextProvider extends Component{
	constructor(props){
    super(props);

    let mediaQuery = window.matchMedia('(min-width: 768px)');

    this.state= {
        isDesktop: mediaQuery.matches
    }
    //If window size is less than 768px, treat as movil device
    //This is the Bootstrap media query for md
    //Will be passed as prop to children elements

    //If the query state changes, it will rerender what's needed
    mediaQuery.addListener((e) =>{
        this.setState({isDesktop: e.matches})
    });
  }


  render(){
  	return(
  	  <Context.Provider value={this.state.isDesktop}>
  	    {this.props.children /*Render the childrens declared inside this tag*/}
  	  </Context.Provider>
  	)
  }
}

export {ContextProvider, Context}; //No default here!