//Basic React import
import React, {Component} from 'react';
import {Context} from '../../context/isDesktopContext';
import SmallNav from '../SmallNav/SmallNav';
import BigNav from '../BigNav/BigNav';

class NavClass extends Component{
	render(){
		const isDesktop = this.context
		return(
			isDesktop? <BigNav /> : <SmallNav /> /*Will be rerendered when needed, move this to App*/
		)
	}
}

NavClass.contextType = Context;

export default NavClass;