let reader = new FileReader();

let configEvents = component =>{
  component.onAbort = ()=>{
    console.debug("Aborted")
    component.setState({isLoading: false, isAborted: true});
  }

  reader.onloadstart = ()=>{
    console.debug('Starting to read file');
    component.setState({
      isLoading: true,
      loadingText: `Reading ${component.fileName}, please wait...`
    });
  };

  reader.onerror = (error)=>{
    component.props.changeState({Error: error, UploadSection: false});
  }

  reader.onload = (file)=>{
    component.setState({
      loadingText: 'File Loaded, please wait while we analyze it...',
      isLoaded: true
    });
    console.debug("File Content Loaded");
    analyzeText(file.target.result, component);
  }
}

let analyzeText = (text, component)=>{
  component.props.changeState({File: {
    forFilePreview: text.split(' '),
    forWordEditing: text.split(/[\r\n ]+/),
    fileTitle: component.fileName
  }, UploadSection: false});
}

export {configEvents, reader, analyzeText};